import { Link } from "react-router-dom";

export default function Header() {
  return (
    <div className="header">
      <Link to="/matrix" className={"link"}>
        <p>  (-1)</p>
      </Link>
      <Link to="/kmeans" className={"link"}>
        <p>K-means  (-2)</p>
      </Link>
      <Link to="/algorithm" className={"link"}>
        <p>  Max-min (-3)</p>
      </Link>
      <Link to="/alg4" className={"link"}>
        <p>  (-4)</p>
      </Link>
      <Link to="/neural" className={"link"}>
        <p>  (-5)</p>
      </Link>
      <Link to="/RGZ-II" className={"link"}>
        <p>    (-7)</p>
      </Link>
      <Link to="/alg8" className={"link"}>
        <p>     (-8)</p>
      </Link>
      <Link to="/help" className={"link"}>
        <p></p>
      </Link>
    </div>
  );
}
